import React, { useState } from 'react';
import logo from './logo.jpg';
import './App.scss';
import Question from './components/Question'

const data = [
  {
    id: 1,
    title: 'Tomar agua depois de comer doce da diabetes!',
    awswer: 'mito',
  },
  {
    id: 2,
    title: 'É proibido comer carboidrato depois das 18h!',
    awswer: 'mito',
  },
  {
    id: 3,
    title: 'Comer muito carboidrato pode dar diabetes!',
    awswer: 'verdade',
  },
]

function App() {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [currentQuestion, setCurrentQuestion] = useState(data[currentQuestionIndex])
  const [points, setPoints] = useState(0)
  const [result, setResult] = useState(false)

  function selectAwnser(awnser) {
    if(currentQuestion.awswer === awnser) setPoints(points + 1)

    if(currentQuestionIndex + 1 < data.length) {
      const nextQuestion = currentQuestionIndex + 1
      setCurrentQuestion(data[nextQuestion])
      setCurrentQuestionIndex(nextQuestion)
    } else {
      setResult(true)
    }
  }

  function reset() {
    setCurrentQuestion(data[0])
    setCurrentQuestionIndex(0)
    setResult(false)
    setPoints(0)
  }

  return (
    <main className="wrapper">
      <header className="header">
        <img src={logo} className="header__logo" alt="logo" />
      </header>
      <h2 className="questions-number">
        Questão {currentQuestionIndex + 1} de {data.length}
      </h2>
      { result ? (
        <>
          <p>Você acertou {points}</p>
          <section className="action-buttons action-buttons--centered">
            <button className="btn btn--secondary" onClick={reset}>Refazer</button>
          </section>
        </>
      ) : (
        <>
          <Question question={data[currentQuestionIndex]} />
          <section className="action-buttons">
            <button className="btn btn--secondary" onClick={ () => selectAwnser('mito') }>Mito</button>
            <button className="btn btn--primary" onClick={ () => selectAwnser('verdade') }>Verdade</button>
          </section>
        </>
      )}
    </main>
  );
}

export default App;
